.services-scrolling {
  padding: 0;
  display: flex;
  flex-direction: column;
  justify-content: center; }
  @media (max-width: 1920px) {
    .services-scrolling {
      padding-top: 120px;
      padding-bottom: 80px; } }
  @media (max-width: 1400px) {
    .services-scrolling {
      padding-bottom: 20px; } }
  @media (max-width: 768px) {
    .services-scrolling {
      padding-top: 50px;
      padding-bottom: 50px; } }
  .services-scrolling .container {
    position: relative; }
  .services-scrolling h2 {
    font-weight: 900;
    font-size: 50px;
    line-height: 63px;
    margin-bottom: 60px;
    text-align: left;
    transition: 1s ease all;
    max-width: 50%; }
    @media (max-width: 1024px) {
      .services-scrolling h2 {
        max-width: 100%; } }
    @media (max-width: 480px) {
      .services-scrolling h2 {
        font-size: 32px;
        line-height: 40px; } }
  .services-scrolling .main-info_slider {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    opacity: 1;
    transition: .5s ease all; }
    .services-scrolling .main-info_slider.invisible {
      display: none; }
    .services-scrolling .main-info_slider.hiddenn {
      height: 0;
      opacity: 0; }
    .services-scrolling .main-info_slider .left-block {
      width: calc(50% - 20px); }
    .services-scrolling .main-info_slider .right-block {
      width: calc(50% - 20px); }
    .services-scrolling .main-info_slider p {
      font-weight: 300;
      font-size: 24px;
      line-height: 34px;
      margin-bottom: 16px; }
      .services-scrolling .main-info_slider p:last-child {
        margin-bottom: 0; }
  .services-scrolling .navigation-block {
    position: absolute;
    top: 10px;
    right: 20px;
    opacity: 0;
    transition: .5s ease opacity; }
    .services-scrolling .navigation-block.active {
      opacity: 1; }
    .services-scrolling .navigation-block #currentBlock {
      font-weight: 700;
      font-size: 55px;
      line-height: 50px; }
    .services-scrolling .navigation-block #totalBlock {
      font-weight: 300;
      font-size: 18px;
      line-height: 34px; }
    @media (max-width: 1400px) {
      .services-scrolling .navigation-block {
        display: none; } }
  .services-scrolling #slideContainer {
    margin-top: 160px;
    width: 500%;
    /* to contain 4 panels, each with 100% of window width */
    display: flex;
    flex-wrap: nowrap;
    background-color: white;
    overflow: hidden; }
    @media (max-width: 1920px) {
      .services-scrolling #slideContainer {
        margin-top: 0; } }
    .services-scrolling #slideContainer .panel {
      height: 100%;
      display: flex;
      justify-content: center;
      align-items: flex-start;
      width: 800px; }
      .services-scrolling #slideContainer .panel:first-child {
        margin-left: 5%; }
      .services-scrolling #slideContainer .panel .scrolling-list__item {
        width: 100%;
        height: auto;
        min-height: 650px;
        margin-right: 40px;
        position: relative;
        padding: 68px 68px 40px 120px;
        background: #FAFAFA;
        box-shadow: 0 10px 30px rgba(1, 74, 105, 0.13);
        display: flex;
        flex-direction: column; }
        @media (max-width: 1920px) {
          .services-scrolling #slideContainer .panel .scrolling-list__item {
            padding: 40px 48px 40px 80px;
            min-height: 456px; } }
        .services-scrolling #slideContainer .panel .scrolling-list__item img {
          position: absolute;
          top: 0;
          left: 0;
          opacity: .1;
          width: 66px;
          height: 74px;
          transition: 1s ease opacity; }
        .services-scrolling #slideContainer .panel .scrolling-list__item_information {
          transform: translateY(120px);
          transition: 1s ease transfrom; }
          .services-scrolling #slideContainer .panel .scrolling-list__item_information h3 {
            font-weight: 700;
            font-size: 32px;
            line-height: 40px;
            text-transform: uppercase;
            margin-bottom: 20px;
            color: #00A6EB; }
          .services-scrolling #slideContainer .panel .scrolling-list__item_information p {
            display: block;
            font-weight: 300;
            font-size: 24px;
            line-height: 34px;
            margin-bottom: 16px;
            width: 100%; }
            @media (max-width: 1920px) {
              .services-scrolling #slideContainer .panel .scrolling-list__item_information p {
                font-size: 22px;
                line-height: 30px; } }
          .services-scrolling #slideContainer .panel .scrolling-list__item_information p:last-child {
            display: none;
            margin-bottom: 0; }
        .services-scrolling #slideContainer .panel .scrolling-list__item.element-show {
          background: #E3F7FF; }
          .services-scrolling #slideContainer .panel .scrolling-list__item.element-show img {
            opacity: 1; }
          .services-scrolling #slideContainer .panel .scrolling-list__item.element-show .scrolling-list__item_information {
            transform: translateY(0);
            transition: 1s ease transform; }
            .services-scrolling #slideContainer .panel .scrolling-list__item.element-show .scrolling-list__item_information h3 {
              color: #121212; }
            .services-scrolling #slideContainer .panel .scrolling-list__item.element-show .scrolling-list__item_information p:last-child {
              display: block; }
    @media (max-width: 1400px) {
      .services-scrolling #slideContainer {
        display: none; } }
  .services-scrolling .card-list {
    display: none; }
    @media (max-width: 1400px) {
      .services-scrolling .card-list {
        display: flex;
        align-content: center;
        justify-content: space-between;
        flex-wrap: wrap; }
        .services-scrolling .card-list__item {
          width: calc(100% / 2 - 16px);
          padding: 40px 40px 40px 64px;
          background-color: #FAFAFA;
          border-bottom: 2px solid #CBECFF;
          box-shadow: 0 10px 30px rgba(1, 74, 105, 0.13);
          position: relative;
          margin-bottom: 60px; }
          .services-scrolling .card-list__item img {
            position: absolute;
            top: 0;
            left: 0;
            width: 54px;
            height: 62px; }
          .services-scrolling .card-list__item .information h3 {
            font-weight: 700;
            font-size: 26px;
            line-height: 34px;
            text-transform: uppercase;
            color: #00A6EB;
            margin-bottom: 24px; }
          .services-scrolling .card-list__item .information p {
            font-weight: 300;
            font-size: 20px;
            line-height: 30px;
            color: #121212;
            margin-bottom: 16px; }
            .services-scrolling .card-list__item .information p:last-child {
              margin-bottom: 0; } }
    @media (max-width: 820px) {
      .services-scrolling .card-list__item {
        width: 100%;
        padding: 24px 15px 24px 48px;
        margin-bottom: 32px; }
        .services-scrolling .card-list__item img {
          width: 41px;
          height: 46px; }
        .services-scrolling .card-list__item .information h3 {
          margin-bottom: 16px; }
        .services-scrolling .card-list__item .information p {
          margin-bottom: 12px; } }
    @media (max-width: 480px) {
      .services-scrolling .card-list__item .information h3 {
        max-width: 230px; } }
